import React, { useState, useEffect } from 'react';
import { Link, useNavigate,useParams  } from "react-router-dom";
import axios from 'axios';
const Detail = ({ match,authData, updateCartItems }) => {
    const [book, setBook] = useState(null);
    const [error, setError] = useState(null);
    const { id } = useParams();
    const [quantity, setQuantity] = useState(1);
    const navigate = useNavigate();

    useEffect(() => {
      const fetchBookDetail = async () => {
        try {
          const response = await axios.get(`http://localhost:8080/book/getBookById/${id}`);
          setBook(response.data);
          setError(null);
        } catch (error) {
          setError("Khng th ti chi tit sch. Vui lng th li sau.");
          setBook(null);
        }
      };
  
      fetchBookDetail();
    }, [id]);
    
    ////CART
    const handleAddToCart = async (bookId) => {
      try {
        const authData = JSON.parse(localStorage.getItem('auth-data'));
        const userId = authData?.id;
    
        if (!userId) {
          // Nu khng c userId (ngi dng cha ng nhp), iu hng n trang ng nhp
          navigate('/login');
          return;
        }
    
        const price = book.price;
    
        const response = await axios.post('http://localhost:8080/cart/addBookToCart', {
          bookId: bookId,
          userId: userId,
          price: price, // Gi sn phm (thay i theo sn phm)
          total: price * quantity, // Tng gi (thay i theo sn phm v s lng)
          qty: quantity // S dng quantity t state
        });
    
        console.log('Sn phm  c thm vo gi hng:', response.data);
        // iu hng n trang gi hng sau khi thm sn phm thnh cng
        navigate('/cart');
      } catch (error) {
        console.error('Li khi thm sn phm vo gi hng:', error);
      }
    };
    
    //tang gaim sl 
    const handleIncreaseQuantity = () => {
      setQuantity(quantity + 1);
    };
  
    const handleDecreaseQuantity = () => {
      if (quantity > 1) {
        setQuantity(quantity - 1);
      } else {
        alert("S lng phi ln hn hoc bng 1.");
      }
    };
  
    return (
    <>  
  <div className="container-fluid py-5 mt-5">
      <div className="container py-5">
        {error && <p>Error: {error}</p>}
        {book && (
          <div className="row g-4 mb-5">
            <div className="col-lg-8 col-xl-9">
              <div className="row g-4">
                <div className="col-lg-6">
                  <div className="border rounded">
                    <img src={book.img} className="img-fluid rounded" alt="Book" />
                  </div>
                </div>
                <div className="col-lg-6">
                  <h4 className="fw-bold mb-3">{book.title}</h4>
                  <p className="mb-3">Th loi: {book.category.nameC}</p>
                  <p className="mb-3">Tc gi: {book.author}</p>
                  <h5 className="fw-bold mb-3">{book.price}vn</h5>
                  <div className="input-group quantity mb-5" style={{ width: 100 }}>
                    <div className="input-group-btn">
                    <button className="btn btn-sm btn-minus rounded-circle bg-light border" onClick={handleDecreaseQuantity} >

                        <i className="fa fa-minus" />
                      </button>
                    </div>
                    <input type="text" className="form-control form-control-sm text-center border-0" value={quantity} readOnly />
                    <div className="input-group-btn">
                    <button className="btn btn-sm btn-plus rounded-circle bg-light border" onClick={handleIncreaseQuantity}>
                        <i className="fa fa-plus" />
                      </button>
                    </div>
                  </div>
                  <button className="btn btn-primary"  onClick={() => handleAddToCart(book.id)}> Thm gi hng</button>

              
                </div>
                <div className="col-lg-12">
                  <nav>
                    <div className="nav nav-tabs mb-3">
                      <button className="nav-link active border-white border-bottom-0" type="button" role="tab" id="nav-about-tab" data-bs-toggle="tab" data-bs-target="#nav-about" aria-controls="nav-about" aria-selected="true">Description</button>
                    </div>
                  </nav>
                  <div className="tab-content mb-5">
                    <div className="tab-pane active" id="nav-about" role="tabpanel" aria-labelledby="nav-about-tab">
                      <p style={{ textAlign: "left" }}>{book.description}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>

    </>
  )
}

export default Detail
