import React, { useState } from "react";
import axios from "axios";
import ThongtinStyle from '../css/thongtin.module.css'
import { Link, useNavigate } from "react-router-dom";
import { Tab, Row, Col, Nav, Form, Button } from 'react-bootstrap';

const ThongTin = () => {
    const navigate = useNavigate();

    const [oldPassword, setOldPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [message, setMessage] = useState('');


    // X l khi ngi dng nhn nt "Lu"
    const handlePasswordChange = async (e) => {
        e.preventDefault();

        // Ly thng tin ngi dng t localStorage
        const storedUserData = JSON.parse(localStorage.getItem('auth-data'));

        // Kim tra xem storedUserData c gi tr khng
        if (!storedUserData) {
            setMessage('Khng th xc nh ngi dng hin ti');
            return;
        }
        // Kim tra mt khu mi c  iu kin khng
        const passwordRegex = /^(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*]).{8,}$/;
        if (!passwordRegex.test(newPassword)) {
            setMessage('Mt khu mi phi cha t nht 8 k t, bao gm t nht 1 ch Hoa, 1 ch s v 1 k t c bit');
            return;
        }

        // Kim tra xem mt khu c nhp vo c khp vi mt khu hin ti ca ngi dng khng
        if (oldPassword !== storedUserData.password) {
            setMessage('Mt khu c khng chnh xc');
            return;
        }


        //ktra mt khu mi c khp vi nhau
        if (newPassword !== confirmPassword) {
            setMessage('Mt khu xc nhn khng khp vi mt khu mi');
            return;
        }
        // Nu mt khu c ng, tip tc qu trnh i mt khu
        try {
            const response = await axios.put(`http://localhost:8080/change-password/${storedUserData.id}`, {
                oldPassword,
                newPassword,
                confirmPassword,
            });

            // Hin th thng bo thnh cng hoc li t response
            setMessage(response.data.message || 'i mt khu thnh cng');
        } catch (error) {
            // X l li
            setMessage(error.response?.data?.error || ' xy ra li khi i mt khu');
        }

        // Xa d liu trong cc trng nhp liu sau khi x l
        setOldPassword('');
        setNewPassword('');
        setConfirmPassword('');
    };

    return (
        <>
            <div className={ThongtinStyle.body}>
                <div className="main-content-wrap section-ptb my-account-page" style={{ padding: '30px 0' }}>
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <div className="account-dashboard">
                                    <div className="dashboard-upper-info">
                                        <div className="row align-items-center no-gutters">
                                            <div className="col-lg-3 col-md-12">
                                                <div className="d-single-info" style={{ height: 60, padding: '20px 0 20px' }}>
                                                    <p className="user-name">Xin cho<span> tn ng nhp</span></p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md-12 col-lg-9" style={{ marginTop: "50px" }}>
                                            {/* Nav tabs */}
                                            <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                                                <Row>
                                                    <Col sm={3}>
                                                        <Nav variant="pills" className="flex-column">
                                                            <Nav.Item>
                                                                <Nav.Link eventKey="first">Qun l ti khon</Nav.Link>
                                                            </Nav.Item>
                                                            <Nav.Item>
                                                                <Nav.Link eventKey="second">Thng tin c  nhn</Nav.Link>
                                                            </Nav.Item>
                                                            <Nav.Item>
                                                                <Nav.Link eventKey="third">Thng tin t hng</Nav.Link>
                                                            </Nav.Item>
                                                            <Nav.Item>
                                                                <Nav.Link eventKey="fourth">i mt khu</Nav.Link>
                                                            </Nav.Item>
                                                            <Nav.Item>
                                                                <Nav.Link eventKey="five">ng xut</Nav.Link>
                                                            </Nav.Item>
                                                        </Nav>
                                                    </Col>
                                                    <Col sm={9}>
                                                        <Tab.Content>
                                                            <Tab.Pane eventKey="first">
                                                                <h3>Qun l ti khon </h3>
                                                                <div className="row">
                                                                    <div className="d111 col-md-4">
                                                                        <h6>Thng tin c nhn <span className="sp111"></span></h6>
                                                                        <div className="sp111">
                                                                            <span>uname</span><br></br>
                                                                            <span>fullName</span><br></br>
                                                                            <span>email</span>
                                                                        </div>
                                                                    </div>
                                                                    <div className="d111 col-md-8 " style={{ borderLeft: 'rgb(255, 255, 255) 10px solid' }}>
                                                                        <h6>Thng tin t hng <span className="sp111" /></h6>
                                                                        <div className="sp111">
                                                                            <span>uname</span><br></br>
                                                                            <span>phone</span><br></br>
                                                                            <span>address</span>
                                                                        </div>

                                                                    </div>
                                                                </div>
                                                                <div className="table-responsive d222">
                                                                    <h5>n hng</h5>
                                                                    <table className="table">
                                                                        <thead>
                                                                        <tr>
                                                                            <th>ID</th>
                                                                            <th>Tn ngi nhn</th>
                                                                            <th>Ngy t hng</th>
                                                                            <th>Trng thi</th>
                                                                            <th>Tng tin</th>
                                                                            <th>Chi tit</th>
                                                                        </tr>
                                                                        </thead>
                                                                        <tbody>
                                                                        <tr>
                                                                            <td> c duyt</td>
                                                                        </tr>
                                                                        </tbody>
                                                                    </table>
                                                                </div>
                                                            </Tab.Pane>
                                                            <Tab.Pane eventKey="second">
                                                                <h3>Thng tin c nhn</h3>
                                                                <div id="save_111">
                                                                    <div className="row">
                                                                        <div className="col-6">
                                                                            <p className="p441"><b>Tn ng nhp</b></p>
                                                                            <p>uname</p>
                                                                        </div>
                                                                        <div className="col-6">
                                                                            <p className="p441"><b>H v tn</b></p>
                                                                            <p>fullName</p>
                                                                        </div>
                                                                        <div className="col-6">
                                                                            <p className="p441"><b>Email</b></p>
                                                                            <p>email</p>
                                                                        </div>
                                                                        <div className="col-6">
                                                                            <p className="p441"><b>S in thoi</b></p>
                                                                            <p>phone</p>
                                                                        </div>
                                                                        <div className="col-6">
                                                                            <p className="p441"><b>a ch</b></p>
                                                                            <p>address</p>
                                                                        </div>


                                                                    </div>

                                                                </div>
                                                            </Tab.Pane>
                                                            <Tab.Pane eventKey="fourth">
                                                                <h3>i mt khu</h3>
                                                                <form onSubmit={handlePasswordChange}>
                                                                    <label>Mt khu c:</label>
                                                                    <input type="password" value={oldPassword} onChange={(e) => setOldPassword(e.target.value)} />
                                                                    <label>Mt khu mi:</label>
                                                                    <input type="password" value={newPassword} onChange={(e) => setNewPassword(e.target.value)} />
                                                                    <label>Nhp li mt khu mi:</label>
                                                                    <input type="password" value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} />
                                                                    <button type="submit">Lu</button>
                                                                </form>
                                                                {message && <p>{message}</p>}
                                                            </Tab.Pane>
                                                            <Tab.Pane eventKey="third">

                                                            </Tab.Pane>
                                                        </Tab.Content>
                                                    </Col>
                                                </Row>
                                            </Tab.Container>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>




        </>
    )
}

export default ThongTin
