import React, { useEffect, useState } from "react";
import loginStyles from '../css/login.module.css';
import { Link, useNavigate } from "react-router-dom";
import axios from 'axios';

const Login = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post('http://localhost:8080/login', formData);
      console.log(response.data.id)

      if (response.data.id) {
        //lu tt user  vo bin localStorage
        localStorage.setItem('auth-data',JSON.stringify(response.data));

        console.log("ng nhp thnh cng");
        // X l khi ng nhp thnh cng, chuyn hng n trang chnh sau khi ng nhp
      window.location.reload('/');
      } else {
        setError('Email hoc mt khu khng chnh xc');
      }
    } catch (error) {
      setError('ng nhp khng thnh cng');
    }
  };
  useEffect(() => {
    //bin  lu trong localStorage l "auth-data"
    //check xem  tn ti cha, ri th  ng nhp t ng chuyn qua home
    if(localStorage.getItem('auth-data')) {
      navigate('/')
    }
  },[])

  return (
      <div className={loginStyles.body}>
        <div className={loginStyles.main}>
          <h2 style={{ textAlign: "center" }}>ng Nhp</h2>
          <form onSubmit={handleSubmit}>
            <label htmlFor="email">Email:</label>
            <input
                type="text"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                required
            />

            <label htmlFor="password">Mt khu:</label>
            <input
                type="password"
                id="password"
                name="password"
                pattern="^(?=.*\d)(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9])\S{8,}$"
                title="Password must contain at least one number, one alphabet, one symbol, and be at least 8 characters long"
                value={formData.password}
                onChange={handleChange}
                required
            />

            {error && <p style={{ color: 'red' }}>{error}</p>}
            <button type="submit">ng Nhp</button>
            <p className={loginStyles.quen} style={{ textAlign: "right" }}>
              <a className={loginStyles.ab} href="">
                Qun Mt Khu?
              </a>
            </p>

            <p>
              Bn cha c ti khon{" "}
              <Link className={loginStyles.l} to="/register">
                ng K
              </Link>
            </p>
          </form>
        </div>
      </div>
  );
};

export default Login;
